/* Bootstrap overrides */
.container-fluid {
  padding-left: 0px; padding-right: 0px;
  font-family: 'Righteous';
  background-image: url(./img/bg.jpeg);
  background-size: cover;
}
/* App */
.App {
  background-color: #FFFFFF;
  text-align: center;
  min-height: 100vh;  
}
.App-logo {
  height:  10vmin;
}
.App-header {  
 /* color: #1F4C73;  dark blue */
 /* color: #387CA6;  light blue */
 /* color: #96D2D9;  extra skylight blue */
 /* color: #F2E8C9;  beigy yellow */
 /* color: #402F11;  dark brown */
  height:  10vmin; 
  display: flex;
  flex-direction: column;
  align-items: left; 
  font-size: calc(10px + 2vmin); 
  background-color: #ECECEC;  
  opacity: .9;
}
.navbar {
  padding: 0;
}
.App-link {
  color: #1F4C73;
  background-color: #F2E8C9;
  border: 3px solid #402F11;
}
.blockquote {
  background-color: #402F11; 
  color: #96D2D9;
  padding-left: 5px; padding-right: 5px;
}
.blockquote-footer{
  font-family: 'Parisienne'; 
  font-size: 120%; 
  color: #96D2D9; 
}
.App-content {
  height: 90vmin;
  background-color: #ECECEC; 
  opacity: .8;
}
.jumbotron {
  padding-top: 1vh;
  padding-bottom: 1vh;
  margin-bottom: 0;
}
.formFieldsPadding {
  padding: 10px;
  width: 35vw;
}
.btn-success{
  margin-left: 1vw;
  background-color: #1F4C73;
  border:0;
} 